<template>
  <a-space>
    <a-range-picker
      :ranges="ranges"
      :disabled="currentDisabled"
      :disabled-date="disabledDate"
      :default-value="defaultValue"
      :inputReadOnly="true"
      :allowClear="false"
      @change="onChange"
    />
    <a-icon type="swap" v-if="is_show" />
    <a-dropdown>
      <a-menu slot="overlay" @click="handleMenuClick">
        <a-menu-item key="1"></a-menu-item>
        <a-menu-item key="2"></a-menu-item>
        <a-menu-item key="3"></a-menu-item>
        <a-menu-item key="4"></a-menu-item>
      </a-menu>
      <a-button v-if="!is_show">+ <a-icon type="down" /> </a-button>
      <a-range-picker
        :ranges="ranges_1"
        :default-value="compareDefaultValue"
        :disabled-date="disabledDate"
        :open="is_open"
        v-model="contrastTime"
        :inputReadOnly="true"
        :allowClear="compareAllowClear"
        @calendarChange="handCalendarChange"
        @change="handOnChange"
        @openChange="handOpenChange"
        v-if="is_show"
      />
    </a-dropdown>
  </a-space>
</template>

<script>
import moment from "moment";
export default {
  name: "MjDateSelect",
  props: {
    fnSelectDate: {
      type: Function,
      default: function () {},
    },
    compareAllowClear: {
      type: Boolean,
      default: true,
    },
    currentDisabled: {
      type: Boolean,
      default: false,
    },
    defaultValue: {
      type: Array,
      default() {
        return [];
      },
    },
    compareDefaultValue: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      // 
      ranges: {
        : () => [moment(), moment()],
        : () => [
          moment().startOf("day").subtract(1, "days"),
          moment().endOf("day").subtract(1, "days"),
        ],
        7: () => [moment().startOf("day").subtract(6, "days"), moment()],
        30: () => [
          moment().startOf("day").subtract(29, "days"),
          moment(),
        ],
        : () => [
          moment().startOf("month"),
          moment().endOf("month").endOf("month"),
        ],
        : () => [
          moment()
            .month(moment().month() - 1)
            .startOf("month"),
          moment()
            .month(moment().month() - 1)
            .endOf("month"),
        ],
      },
      // 
      ranges_1: {},
      is_show: 0,
      is_open: false,
      contrastTime: [], // 
      startTime: null, // 
      endTime: null, // 
      timeStr: null, // 
      diffTime: 0,
      ekey: null,
      // 
      disabledDate(current) {
        // Can not select days before today and today
        return current && current > moment().endOf("day");
      },
      formatDate: "YYYY-MM-DD",
    };
  },
  methods: {
    /**
     * 
     */
    moment,
    /**
     * 
     */
    onChange(dates, dateStrings) {
      // 
      this.startTime = dateStrings[0];
      // 
      this.endTime = dateStrings[1];
      // 
      this.diffTime = dates[1].diff(dates[0], "days");
      // 
      if (this.ekey) {
        if (this.ekey > 3) {
          this.setContrastTime();
        } else {
          this.handleMenuClick({
            key: this.ekey,
          });
        }
      } else {
        this.selectCallback();
      }
    },
    /**
     * 
     */
    handOpenChange() {
      if (this.contrastTime.length === 0) {
        this.is_show = false;
      } else {
        this.is_open = false;
      }
    },
    /**
     * 
     */
    handCalendarChange(dates) {
      this.is_open = false;
      if (dates.length === 1) {
        this.timeStr = dates[0].format(this.formatDate);
      }
      // 
      this.setContrastTime();
    },
    /**
     * 
     */
    handOnChange(dates) {
      if (dates.length === 0) {
        this.is_show = false;
        this.ekey = null;
      }
      this.selectCallback();
    },
    /**
     * 
     */
    setContrastTime() {
      this.contrastTime = [
        moment(this.timeStr),
        this.disabledDate(moment(this.timeStr).add(this.diffTime, "d"))
          ? moment().endOf("day")
          : moment(this.timeStr).add(this.diffTime, "d"),
      ];
      this.selectCallback();
    },
    /**
     * 
     */
    handleMenuClick(e) {
      const key = parseInt(e.key);
      this.ekey = key;
      this.is_show = true;
      switch (key) {
        case 1:
          this.contrastTime = [
            moment(this.startTime).subtract(1, "d"),
            moment(this.endTime).subtract(1, "d"),
          ];
          break;
        case 2:
          this.contrastTime = [
            moment(this.startTime).subtract(1, "w"),
            moment(this.startTime).subtract(1, "w").add(this.diffTime, "d"),
          ];
          break;
        case 3:
          this.contrastTime = [
            moment(this.startTime).subtract(1, "M"),
            moment(this.startTime).subtract(1, "M").add(this.diffTime, "d"),
          ];
          break;
        case 4:
          this.is_open = true;
          break;
        default:
          break;
      }
      key !== 4 && this.selectCallback();
    },
    /**
     * 
     */
    selectCallback() {
      this.fnSelectDate({
        start_time: this.startTime,
        end_time: this.endTime,
        compare_start_time:
          this.contrastTime.length > 0
            ? this.contrastTime[0].format(this.formatDate)
            : null,
        compare_end_time:
          this.contrastTime.length > 0
            ? this.contrastTime[1].format(this.formatDate)
            : null,
      });
    },
  },
  /**
   * 
   */
  watch: {
    defaultValue: {
      immediate: true,
      handler(n) {
        // 
        this.startTime = n[0].format(this.formatDate);
        // 
        this.endTime = n[1].format(this.formatDate);
        // 
        this.diffTime = n[1].diff(n[0], "days");
      },
    },
    compareDefaultValue: {
      immediate: true,
      handler(n) {
        if (n.length > 0) {
          this.handleMenuClick({
            key: 1,
          });
        }
      },
    },
  },
};
</script>
