<template>
  <div>
    <div id="wangeditor"></div>
  </div>
</template>
<script>
import E from "wangeditor";
export default {
  name: "MjWangeditor",
  data() {
    return {
      content: "",
    };
  },
  props: {
    option: {
      type: Object,
      default: () => {
        return {};
      },
    },
    editorText: {
      type: [String, Number],
    },
    editCallback: {
      type: Function,
      default: function () {},
    },
  },
  watch: {
    editorText: {
      immediate: true,
      handler(n) {
        if (this.editor) {
          this.editor.txt.html.call(this, n);
        }
      },
    },
  },
  mounted() {
    // 
    this.setEditor();
  },
  beforeDestroy() {
    //  API 
    this.editor.destroy();
    this.editor = null;
  },
  methods: {
    setEditor() {
      this.$nextTick(() => {
        const editor = new E("#wangeditor");
        editor.config.onchangeTimeout = 500;
        editor.config.excludeMenus = ["fontName", "fontSize"];
        editor.config.placeholder = "";
        editor.config.showFullScreen = false;
        editor.config.uploadImgMaxLength = 1;
        editor.config.withCredentials = true;
        editor.config.uploadFileName = "pic"; // 
        editor.config.uploadImgServer = "/temple/uploadImage"; // 
        // 
        editor.config.onchange = (newHtml) => {
          this.content = newHtml.replaceAll(
            /color="([\w\d#]*)"/g,
            'style="color:$1"'
          ); // 
          this.editCallback(this.content); // 
        };
        editor.config.uploadImgHooks = {
          // 
          before: function (xhr) {
            console.log(xhr);
            // 
            return {
              prevent: false,
              msg: "",
            };
          },
          // 
          success: function (xhr) {
            console.log("success", xhr);
          },
          // 
          fail: function (xhr, editor, resData) {
            console.log("fail", resData);
          },
          //  http 
          error: function (xhr, editor, resData) {
            console.log("error", xhr, resData);
          },
          // 
          timeout: function (xhr) {
            console.log("timeout", xhr);
          },
          // 
          //  { errno: 0, data: [...] }  customInsert
          customInsert: function (insertImgFn, result) {
            insertImgFn(result.res);
          },
        };
        editor.config = Object.assign({}, editor.config, this.option);
        editor.create();
        // 
        editor.txt.html(this.editorText);
        this.editor = editor;
      });
    },
  },
};
</script>
