<template>
  <div>
    <!-- <img style="width: 100%" :src="previewImage" @click="handClick" /> -->
    <a-modal
      :visible="previewVisible"
      :width="width"
      :footer="null"
      @cancel="handleCancel"
    >
      <img alt="example" ref="img" @load="handLoad" :src="previewImage" />
    </a-modal>
  </div>
</template>
<script>
export default {
  name: "MjPreview",
  props: {
    // 
    previewImage: {
      type: String,
      default: "",
    },
    // 
    previewVisible: {
      type: Boolean,
      default: false,
    },
    // 
    previewCancel: {
      type: Function,
      default() {},
    },
  },
  created() {},
  data() {
    return {
      width: 0,
    };
  },
  methods: {
    handleCancel() {
      // this.previewVisible = false;
      this.previewCancel();
    },
    handLoad() {
      this.width = this.$refs.img.width + 48;
    },
  },
};
</script>
